import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form, message } from 'antd';
import { BtnExport } from 'components/common/button';
import {
  dowloadSocialStructure,
  dowloadTownStuden,
  dowloadDistrictStudent,
  dowloadForeignStudent,
  dowloadSubsidizeObjectStudent,
  dowloadPolicyObjectStudent,
} from 'services/ReportFileStudent';

import { exportPaperMissing, exportFullPaper } from 'services/class';

import FilterPathEducate from 'components/common/input/FilterPathEducate';

@Form.create({ name: 'FormSearchStudent' })
class ReceiveAdmissionPapersPage extends Component {
  state = {};

  // Cơ cấu xã hội
  dowloadFileSocialStructure = () => {
    this.props.form.validateFields((err, values) => {
      dowloadSocialStructure(values)
        .then(() => {})
        .catch(error => {
          message.error(error.response.data.message);
        });
    });
  };
  // TK theo tỉnh
  dowloadFileTownStuden = () => {
    this.props.form.validateFields((err, values) => {
      dowloadTownStuden(values)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };
  // TK theo huyện thị
  dowloadFileDistrictStudent = () => {
    this.props.form.validateFields((err, values) => {
      dowloadDistrictStudent(values)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };
  // Sinh viên nước ngoài
  dowloadFileForeignStudent = () => {
    this.props.form.validateFields((err, values) => {
      dowloadForeignStudent(values)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };
  // Sinh viên trợ cấp
  dowloadFileSubsidizeObjectStudent = () => {
    this.props.form.validateFields((err, values) => {
      dowloadSubsidizeObjectStudent(values)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };
  // Sinh viên chính sách
  dowloadFilePolicyObjectStudent = () => {
    this.props.form.validateFields((err, values) => {
      dowloadPolicyObjectStudent(values)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };

  exportFullPaperFile = () => {
    this.props.form.validateFields((err, values) => {
      exportFullPaper(values.classId)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };
  exportPaperMissingFile = () => {
    this.props.form.validateFields((err, values) => {
      exportPaperMissing(values.classId)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };

  render() {
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      classId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Lớp',
          },
        ],
      },
    };
    return (
      <Fragment>
        <Card title="Báo cáo hồ sơ SV">
          <Row>
            <Col span={24}>
              <Form layout="vertical" className="form-inline-label">
                <Row gutter={24}>
                  <FilterPathEducate
                    form={this.props.form}
                    optionFieldDecorator={optionFieldDecorator}
                  />
                </Row>
                <Row>
                  <Col span={24}>
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="Cơ cấu xã hội"
                      onClick={() => this.dowloadFileSocialStructure()}
                    />
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="TK theo tỉnh"
                      onClick={() => this.dowloadFileTownStuden()}
                    />
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="TK theo huyện thị"
                      onClick={() => this.dowloadFileDistrictStudent()}
                    />
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="Sinh viên nước ngoài"
                      onClick={() => this.dowloadFileForeignStudent()}
                    />
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="Sinh viên trợ cấp"
                      onClick={() => this.dowloadFileSubsidizeObjectStudent()}
                    />
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="Sinh viên chính sách"
                      onClick={() => this.dowloadFilePolicyObjectStudent()}
                    />
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="Đủ hồ sơ nhập học"
                      onClick={() => this.exportFullPaperFile()}
                    />
                    <BtnExport
                      style={{ marginRight: 10, marginTop: 10 }}
                      title="Thiếu hồ sơ nhập học"
                      onClick={() => this.exportPaperMissingFile()}
                    />
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



