import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudents = dataSearch =>
  request({
    prefix,
    url: `/Student/Search`,
    params: { ...dataSearch },
    method: 'GET',
  });
// Cơ cấu xã hội:
export const dowloadSocialStructure = paramsSearch => {
  console.log('paramsSearch', paramsSearch);
  return requestDownload({
    defaultFileName: 'ExportReportSocialStructure.pdf',
    prefix,
    url: `/Student/ExportReportSocialStructure`,
    params: { ...paramsSearch },
    method: 'POST',
  });
};

